@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav{padding:0 10vmin;display:flex;font-family:'Poiret One', cursive;justify-content:space-between}.nav__btn{font-family:'Poiret One', cursive;font-weight:bold;color:white;width:30px;height:30px;border-radius:50%;border:none;padding:0;position:relative}.nav__btn_wrapper{-webkit-filter:grayscale(80%);filter:grayscale(80%);transition:-webkit-filter 1s;transition:filter 1s;transition:filter 1s, -webkit-filter 1s}.nav__btn_wrapper:hover{-webkit-filter:grayscale(0);filter:grayscale(0)}.nav__btn:focus{outline:none}.nav__btn:hover{cursor:pointer}.nav__btn_img{width:30px;height:30px;z-index:0;box-sizing:border-box;border-radius:50%;border:2px solid white}.nav__btn_img-active{border:none}.switch{position:relative;display:inline-block;width:60px;height:34px}.switch input{opacity:0;width:0;height:0}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background:red;transition:.4s}.slider:before{position:absolute;content:"";height:26px;width:26px;left:4px;bottom:4px;background-color:white;transition:.4s}input:checked+.slider{background-color:greenyellow}input:focus+.slider{box-shadow:0 0 1px #2196F3}input:checked+.slider:before{transform:translateX(26px)}.slider.round{border-radius:34px}.slider.round:before{border-radius:50%}

.NavBar{width:50%;display:flex;justify-content:space-between;text-align:center}

